@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300&display=swap');

$color-blog: #ffff;

/* NAVBAR VARIABLES */
/* $bg-color-navbar: #B11F24; */
$bg-color-navbar:  #D40005;
$bg-color-navbar-active: #000000;
$border-color-active: #000000;
/* $border-color-navbar: #B11F24; */
$font-color-navbar: rgb(0, 0, 0);
$font-color-navbar-hover: #CC262A;
$font-color-navbar-active: #FF585C;

/* NAVBAR CATEGORIAS VARIABLES */
$bg-color-navbar-categorias:  #FFFFFF;
$font-color-navbar-categorias: #CC262A;
$font-color-navbar-categorias-hover: #FF585C;

//MINI BANNER SOBRE FOOTER
$bg-color-banner: #D40005;
$bg-color-font-banner:  rgb(255, 255, 255);

//VARIABLE DE BUSCADOR-SEARCH
$bg-color-search: #D40005;
$bg-color-search-hover:#FF585C;

/* CARDS VARIABLES */
$bg-color-cards-productos:  #F7F7F7;

/* SPINER VARIABLES */
$color-spiner: #CC262A;

/* BUTTONS VARIABLES */
$color-botones: #D40005;
$color-botones-hover: #CC262A;
$color-botones-font:  #F7F7F7;
$color-botones-secundario: #FF585C;
$color-botones-secundario-text: #000000;
$color-botones-secundario-border: #CC262A;
$color-botones-sec-hover: #f8f8f8;

/* STEPS VARIABLES */
$bg-color-steps: #FF585C;
$font-color-steps-inactive: #ffff;
$bg-color-steps-active: #CC262A;
$border-color-steps: #3D3D3D;

/* TABS VARIABLES */
$color-tabs: #CC262A;
$color-tabs-hover: #FF585C;
$color-tabs-border: #3D3D3D;

$bg-color-tags: #F5F5F5;

/* FOOTER VARIABLES */
$bg-color-footer: #3D3D3D;
$font-color-footer: rgb(255, 255, 255);
$font-color-footer-hover: #D40005;

/* BACKGROUND LAYOUT */
$bg-layout: #F5F5F5;

/* FONT FAMILY STYLES */
h1, h2, h3, h4, h5, h6{
    /* font-family: 'Roboto', sans-serif; */
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}
p, a, li{
    /* font-family: 'Roboto', sans-serif; */
    font-family: 'Montserrat', sans-serif;
    font-weight: 400; 
}

/* LAYOUT */
.bg-layout{
    background-color: $bg-layout;
}

/* NAVBAR STYLES */
.navbar-menu-general{
    background-color: $bg-color-navbar;
}
.navbar-menu-sesion > .ant-menu-item-selected {
    color: $bg-color-navbar!important;
    border-bottom:  solid white!important;
}
.navbar-menu-general > .ant-menu-item-selected{
    background-color: $bg-color-navbar-active!important;
}
.drawer-background .ant-drawer-body{
    background-color: $bg-color-navbar;
    border-color: rgb(255, 255, 255);
}
.drawer-background .ant-drawer-header{
    background-color: $bg-color-navbar;
    border-color: rgb(255, 255, 255);
}
.nav-font-color{
    color: $font-color-navbar;
   /*  border-bottom: none!important; */
} 
.nav-font-color:hover{
    color: $font-color-navbar-hover!important;
    border-bottom: 3px solid $border-color-active!important;
    /* border-top: 2px solid $border-color-active!important;
    border-bottom: none!important; */
}
.nav-font-color-sesion:hover{
    color: $bg-color-navbar!important;
    border-bottom: 3px solid white!important;
    /* border-top: 2px solid $border-color-active!important;
    border-bottom: none!important; */
}
.menu-responsivo-icon{
    color: $font-color-navbar;
}


/* NAVBAR CATEGORIAS STYLES */
.categorias-navbar{
    background-color: $bg-color-navbar-categorias;
}
.categorias-navbar .ant-menu-submenu-title{
    color: $font-color-navbar-categorias!important;
    font-weight: bold!important;
}
.categorias-navbar > .ant-menu-submenu-selected{
    color: $font-color-navbar-categorias!important;
}
.container-subcategorias-nav{
    background-color: $bg-color-navbar-categorias;
    // border-bottom: solid $bg-color-navbar-categorias;
    // border-top: solid $bg-color-navbar-categorias;
}
.nav-font-color-categorias{
    font-weight: bold;
    color: $font-color-navbar-categorias;
    border-bottom: none!important;
} 
.nav-font-color-categorias:hover{
    color: $font-color-navbar-categorias-hover!important;
}
.nav-font-color-categorias:hover{
    color: $font-color-navbar-categorias-hover!important;
}
.nav-font-color-categorias::before{
    color: $font-color-navbar-categorias-hover!important;
}
.container-subcategorias-nav > .ant-menu-submenu-title:hover{
    color: $font-color-navbar-categorias-hover!important;
}

//BORDER DE BUSCADOR
.border-color-search-boton{
    border: 2.5px solid $bg-color-search;
    border-left: none;
}
.border-color-search-input{
    border: 1.5px solid $bg-color-search;
}
.border-color-search:hover{
    border: 2.5px solid $bg-color-search-hover;
}

//COLOR DE BORDES DE LAS CARDS
.contenedor-card-producto-principal, .contenedor-card-producto-secundario, .contenedor-card-oferta-gigante, .item-menu-paginas{
    border-right: 1px solid #e0e0e0;
}

/* SEPARADORES */

