    .textos-home > h2 {
        font-size: 15px;
    }
    .textos-home > p {
        font-size: 10px;
    }
} */

/* CARDS */
.contenedor-card-body > .ant-card-body{
    padding: 0px!important;
    background-color: $bg-color-cards-productos; 
    min-height: 90px;
}
.mini-contenedor-card-body > .ant-card-body{
    min-height: 90px;
    width: auto;
}
.color-contenedor-oferta-especial{
    border: 3px solid $bg-color-navbar;
}

/* SPINER */
.spiner .ant-spin-dot-item{
    background-color: $color-spiner;
}
.spiner{
    color: $color-spiner;
    font-family: 'Montserrat', sans-serif;
}

/* BUTTONS */
.color-boton{
    background-color: $color-botones;
    border-color: $color-botones;
    color: $color-botones-font!important;
}
.color-boton:hover{
    background-color: $color-botones-hover;
    border-color: $color-botones;
}
.color-boton:focus{
    background-color: $color-botones-hover;
    border-color: $color-botones;
}

//BUTTONS SECUNDARIOS
.color-boton-sec{
    background-color: $color-botones-secundario!important;
    border-color: $color-botones-secundario-border!important;
    color: $color-botones-secundario-text!important;

}
.color-boton-sec:hover{
    background-color:  $color-botones-sec-hover;
    border-color: $color-botones-sec-hover;
}
.color-boton-sec:focus{
    background-color:  $color-botones-sec-hover;
    border-color: $color-botones-sec-hover;
}

//SOLO FUENTES
.color-fonts{
    color: $color-botones!important;
}
.color-fonts:hover{
    color: $border-color-active;
}

// COLOR DE TAGS
.color-tags{
    background-color: $bg-color-tags;
}
.color-border-tags{
    border: 1px solid $border-color-active;
}


//ICONOS DE AVANCES
.steps-colors .ant-steps-item-icon{
    background-color:  $bg-color-steps;
    border-color: $border-color-steps;
    
}
.steps-colors .ant-steps-item-process > .ant-steps-item-container > .ant-steps-item-icon{
    background-color:  $bg-color-steps-active;
}
.steps-colors .ant-steps-icon{
    color: $font-color-steps-inactive!important;

}
.steps-colors .ant-steps-item-title::after{
    border-color:  $bg-color-steps-active!important;
    background-color:  $bg-color-steps-active!important;
}


/* TABS */
.tabs-colors .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn{
    color: $color-tabs;
}

.tabs-colors .ant-tabs-tab:hover{
    color: $color-tabs;
}
.tabs-colors .ant-tabs-ink-bar{
    background-color: $color-tabs-border!important;
}


/*  FOOTER STYLES */
.bg-footer{
    background-color: $bg-color-footer;
}
.footer-font-color {
    color: $font-color-footer;
}
.footer-font-color:hover {
    color: $font-color-footer;
}
.footer-border {
    border-color: $font-color-footer;
}

.info-recomended-sizes{
    width: 50%;
}

// G A L E R I A
.image-gallery-thumbnail.active, .image-gallery-thumbnail:hover, .image-gallery-thumbnail:focus{
    border: .5px solid $bg-color-navbar;
}


//B L O G S
.list-hover:hover .titulo-blog{
    color: $bg-color-navbar-active!important;
    transition: .3s;
}
.list-hover:hover{
    box-shadow: 0px 0px 13px -2px $bg-color-navbar!important;
    transition: .3s;
}

//BANNER SOBRE FOOTER
.container-mini-banner{
    background-color: $bg-color-banner;
}
.card-informativa{
    color: $bg-color-font-banner;
}


//VARIABLES DE TITULOS GENERALES PARA TODA LA PAGINA

.font-prin{
    font-size: 24px;
}
.font-secun{
    font-size: 18px;
}
.font-descrip{
    font-size: 16px;
}
.font-peque{
    font-size: 14px;
}

.precio-prin{
    font-size: 30px;
}
.font-vista-prod{
    font-size: 15px;
}
.font-foot{
    font-size: 14px;
}

.font-cates{
    font-size: 19px;
    text-transform: uppercase;
    letter-spacing: 1.1px;
    // font-weight: bold;
    -webkit-font-smoothing: antialiased;
    font-family: 'AdihausDIN,Helvetica,Arial,sans-serif';
}
.font-nav{
    font-size: 14px;
    letter-spacing: 1px;
    // font-weight: bold;
    -webkit-font-smoothing: antialiased;
    font-family: 'AdihausDIN,Helvetica,Arial,sans-serif';
}

.font-carrito{
    font-size: 24px;
}
.font-sec-car{
    font-size: 18px;
}
.font-des-car{
    font-size: 15px;
}


